const Post = ({ data, pageContext }) => {
  const { next, prev } = pageContext;
  const post = data.markdownRemark;
  const image = post.frontmatter.cover.childImageSharp.fluid;

  const Fimage = post.frontmatter.youtube_link.childImageSharp.fluid;
  const Uimage = post.frontmatter.unitystore_link.childImageSharp.fluid;
  const Iimage = post.frontmatter.itch_link.childImageSharp.fluid;
  const Dimage = post.frontmatter.docs_link.childImageSharp.fluid;

  //const mainimage = post.frontmatter.main_image.childImageSharp.fixed;
  //const title = post.frontmatter.title;
  //const mt = post.frontmatter.main_text;
  //const date = post.frontmatter.date;
  //const html = post.html;

  var LinedynamicWidth;
  var LineDynamicBorder;
  var dynamicWidth;
  var dynamicImageHeight;
  var dynamicHeight;
  var intViewportWidth;
  var lineMargin;
  var mainLinksDynamicWidth;
  var mainLinksDynamicPosBott;
  var mainLinksDynamicPosBottom;
var mainLinksLeft;
var mainLinksTextLeft;
var StartedLinkTop;

  if (typeof window !== `undefined`) {
    intViewportWidth = window.innerWidth;
  }  

  if (intViewportWidth > 500) 
  {
    dynamicWidth = 'calc(100% - 45%)';
    dynamicImageHeight = 'calc(100% - 20%)';
    LinedynamicWidth = '45%';
    dynamicHeight = '50%';
    LineDynamicBorder = '1px solid white';
    lineMargin = '-25.5vh auto 20% 50%';
    mainLinksDynamicWidth = ['3%','3.5%','3%','3%'];
    mainLinksDynamicPosBott = '12vh';
    mainLinksDynamicPosBottom = '6vh';
    mainLinksLeft = ['20%','27%','34%','41%'];
    mainLinksTextLeft = ['20%','27.7%','34.5%','41.1%'];
    StartedLinkTop = 'calc(100vh - 1vw)';
  }
  else 
  {
    dynamicWidth = '100%';
    dynamicImageHeight = '45%';
    LinedynamicWidth = '100%';
    dynamicHeight = '0%'
    LineDynamicBorder = '0px transparent';
    lineMargin = '-35vh 3% auto';
    mainLinksDynamicWidth = ['12%','14%','12%','12%'];
    mainLinksDynamicPosBott = '40vh';
    mainLinksDynamicPosBottom = '34vh';
    mainLinksLeft = ['10%','30%','50%','70%'];
    mainLinksTextLeft = ['10.6%','32%','52%','71%'];
    StartedLinkTop = 'calc(100vh - 10vw)';
  }  

  return (  

    <Layout>
{/* <Img fluid = {image}/> */}

{/* <NavBar style = {{color: "#FFF !important"}} /> */}
      {/* <a href="#" style={{ color: "#FFF4F2", position: "absolute", left: mainLinksTextLeft[0], bottom: mainLinksDynamicPosBottom, right: "0" }}><font color="#FFF4F2">forum</font></a>
      <a href="#" style={{ color: "#FFF4F2", position: "absolute", left: mainLinksTextLeft[1], bottom: mainLinksDynamicPosBottom, right: "0" }}><font color="#FFF4F2">docs</font></a>
      <a href="#" style={{ color: "#FFF4F2", position: "absolute", left: mainLinksTextLeft[2], bottom: mainLinksDynamicPosBottom, right: "0" }}><font color="#FFF4F2">store</font></a>
      <a href="#" style={{ color: "#FFF4F2", position: "absolute", left: mainLinksTextLeft[3], bottom: mainLinksDynamicPosBottom, right: "0" }}><font color="#FFF4F2">itch.io</font></a> */}
{/* <ClearFix> */}
  

      <MainInfo>    
        <MTLWarper> 
        <Img
        fluid={image}
        imgStyle={{ objectFit: "contain" }}
        style={{gridArea: "a",        
          top: "10%",
          position: "relative",
          margin: "auto",
          width: "80vw",
          height:"80%"
        }}
      />      
      <Mlink>
        <InnerGridMainLinks>
          
  <Img
    fluid={Fimage}
    imgStyle={{ objectFit: "contain" }}
    style={{ gridArea: "f",  width: "3rem" }}
  />
  <Img
    fluid={Dimage}
    imgStyle={{ objectFit: "contain" }}
    style={{ gridArea: "d",  width: "3rem" }}
  />
  <Img
    fluid={Uimage}
    imgStyle={{ objectFit: "contain" }}
    style={{ gridArea: "u",  width: "3rem" }}
  />
  <Img
    fluid={Iimage}
    imgStyle={{ objectFit: "contain" }}
    style={{ gridArea: "i",  width: "3rem" }}
  />
        </InnerGridMainLinks>
      </Mlink>
      {/* width: dynamicWidth, height: dynamicImageHeight  */}
 {/* //style={{ left: dynamicHeight, width: LinedynamicWidth, borderLeft: LineDynamicBorder, margin: lineMargin }} */}
      <Line >


  {data.markdownRemark.frontmatter.main_text}
  <br></br><br></br>
  <a href= "#GettingStarted" style={{color: "#FFF4F2",left:dynamicHeight, top:StartedLinkTop }}><font color="#EA3C53"> Getting started</font></a>


</Line>
</MTLWarper>
      </MainInfo>
      {/* </ClearFix> */}
      <Info1>

      </Info1>

      <Info2 id = "Info2" >

      </Info2>

      <Info3>

      </Info3>

      <Media>

      </Media>

      <GettingStarted id = "GettingStarted">

      </GettingStarted>

      {/* <Header title={title} cover={image} />
      <Container>
        <Content input={html} />
        <TagsBlock list={post.frontmatter.tags || []} />
      </Container> */}

    </Layout>
  );
};

export default Post;

Post.propTypes = {
  pageContext: PropTypes.shape({
    prev: PropTypes.object,
    next: PropTypes.object,
  }).isRequired,
  data: PropTypes.object.isRequired,
};

const scrollTo = (id) => () => {
  const el = document.querySelector(id)
  if (el) return window.scrollTo(0, el.offsetTop - 20)
  return false
}

export const onRouteUpdate = ({ location: { hash } }) => {
  console.log(location)
  if (hash) {
    window.setTimeout(scrollTo(hash), 10)
  }
}


export const query = graphql`
  query($pathSlug: String!) {
    markdownRemark(frontmatter: { path: { eq: $pathSlug } }) {
      html
      frontmatter {
        date
        title
        tags
        main_text
        main_image {
          childImageSharp {
            
            fixed(              
              quality: 80 
              width: 800                        
            ) {
              ...GatsbyImageSharpFixed_noBase64
            }            
          }
        }
        cover {
          childImageSharp {
            fluid(              
              quality: 100     
              maxWidth: 900         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        youtube_link {
          childImageSharp {
            fluid(              
              quality: 100     
              maxWidth: 100         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        itch_link {
          childImageSharp {
            fluid(              
              quality: 100     
              maxWidth: 100         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        unitystore_link {
          childImageSharp {
            fluid(              
              quality: 100     
              maxWidth: 100         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        docs_link {
          childImageSharp {
            fluid(              
              quality: 100     
              maxWidth: 200         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }

      }
    }
  }
`;





